import React from 'react';
import { HashRouter as Router, Route } from 'react-router-dom' //BrowserRouter
import Header from './components/layout/Header'
import CardList from './components/cardOverview/CardList'
import Board from './components/boardgame/Board'
import Footer from './components/layout/Footer'

import './App.css';


class App extends React.Component {

  render() {
    return (
      <Router>
        <div className="App">

          {/* Header: Title, menu and other shenanigens */}
          <Header />
          
          <Route exact path="/" render={props => (
            <React.Fragment>           
              <Board />
            </React.Fragment>
            )} />
            <Route path="/CardList" component={CardList} />

            <Footer />
        </div>
      </Router>
    );
  }
}

export default App;
